.sec {
  background-color: rgba(255, 255, 255, 0.98);
  border-radius: 0.25rem;
  box-shadow: 3px 3px rgba(0, 0, 0, 0.1);
  position: relative;
  padding: 1em;
  margin: 1em;
}

body:before {
  content: '';
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
  background: url('https://images.unsplash.com/photo-1513836279014-a89f7a76ae86?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1234&q=80')
    no-repeat center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
