.main-feed {
  position: relative;
  font-family: 'Neuton', serif;
  margin: auto;
}
h3 {
  font-size: 2.5rem;
  font-weight: 500;
  line-height: 2rem;
  margin-bottom: 1.3rem;
}
input[type='text'] {
  font-size: 1.1rem;
  vertical-align: middle;
}
.btn {
  font-size: 1.3rem;
  font-weight: 500;
  padding: 0;
  text-align: center;
  padding-bottom: 0.25rem;
}
label {
  font-size: 1.2rem;
}
p {
  font-size: 1.05rem;
  line-height: 1.2rem;
  font-weight: 500;
}
img {
  width: 100%;
  height: auto;
}
.map-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
}
.map-responsive iframe {
  text-align: center;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}
.form-control {
  margin: 1px;
}

.cur {
  cursor: pointer;
}

.no-click {
  cursor: default !important;
  pointer-events: all !important;
}

.person-form {
  background-color: rgba(188, 221, 177, 0.5);
  border-radius: 0.25rem;
  box-shadow: 2px 2px rgba(0, 0, 0, 0.1);
}

.spinner-grow {
  width: 6rem;
  height: 6rem;
  background-color: rgba(115, 163, 100, 0.8);
}

.language-btn {
  position: absolute;
  right: 1em;
  top: 1em;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  z-index: 2;
}

.flag-icon {
  height: 20px;
  width: 20px;
}

ul {
  margin-top: 10px;
  list-style-type: none;
}

#start {
  -webkit-clip-path: polygon(
    0% 0%,
    calc(100% - 3em) 0,
    100% 3em,
    110% 100%,
    0% 110%
  );
}
